*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    /* background-color: #f5f5f5; */
    display: flex;
    justify-content: center;
    /* height: 100vh; */
    font-family: Arial, Helvetica, sans-serif;
    background-color: rgba(240,239,239);
}
.wrapper{
    text-align:center;
    background-color: #fff;
    width: 500px;
    /* height: 100vh; */
    padding: 25px;
    margin: 98px auto;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    /* z-index: 1000000; */
        /* width: 100vw;
        height: 100vh;
        position: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1; */
}
.wrapper h2{
    background-color: #fcfcfc;
    color: #24262b;
    font-size: 24px;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px dotted #333;
}
h4{
    text-align: left;
    padding-bottom: 5px;
    color: #24262b;
}
.input-group{
    margin-bottom: 8px;
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
    padding: 5px 0;
}
.input-box{
    width: 100%;
    margin-right: 12px;
    position: relative;
}
.input-box:last-child{
    margin-right: 0;
}
.name{
    padding: 14px 10px 14px 50px;
    width: 100%;
    background-color: #fcfcfc;
    border: 1px solid #00000033;
    outline: none;
    letter-spacing: 1px;
    transition: 0.3s;
    border-radius: 3px;
    color: #333;
}
.name:focus, dob:focus{
    box-shadow:0 0 2px 1px #24262b;
    border: 1px solid #24262b;;
}
.input-box .icon{
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    color: #333;
    background-color: #f1f1f1;
    border-radius: 2px 0 0 2px;
    transition: 0.3s;
    font-size: 20px;
    pointer-events: none;
    border: 1px solid #00000033;
    border-right: none;
}
.name:focus + .icon{
    background-color: #24262b;
    color: #fff;
    border-right: 1px solid #24262b;
    border: none;
    transition:.8s;
}
.dob{
    width: 30%;
    padding: 14px;
    text-align: center;
    background-color: #fcfcfc;
    transition: 0.3s;
    outline: none;
    border: 1px solid #c0bfbf;
    border-radius: 3px;
}
.radio{
    display: none;
}
.input-box label{
    width: 50%;
    padding: 13px;
    background-color: #fcfcfc;
    display: inline-block;
    /* float: left; */
    text-align-last: center;
    border: 1px solid #c0bfbf;
}
.input-box label:first-of-type{
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    border-right: none;
}
.input-box label:last-of-type{
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}
.radio:checked + label{
    background-color: #24262b;
    color: #fff;
    transition: 0.5s;
}
.input-box select{
    display: inline-block;
    width: 50%;
    padding: 12px;
    background-color: #fcfcfc;
    /* float:left; */
    text-align: center;
    font-size: 16px;
    outline: none;
    border: 1px solid #c0bfbf;
    cursor: pointer;
    transition: all 0.2s ease;
}
.input-box select:focus{
    background-color: #24262b;
    color: #fff;
    text-align-last: center;
}
.btn{
    width: 100%;
    background:transparent;
    border: none;
    background-color: #24262b;
    color: #fff;
    padding: 15px;
    border-radius: 4px;
    font-size: 16px;
    transition: all 0.35s ease;
}
.btn:hover{
    cursor: pointer;
    background-color: #1c1e20;
}
