@media screen and (max-width: 800px){
    .top{
        flex-direction: column;
        justify-content: left;
        align-items: flex-start;
    }
    .logotext{
        margin-left: 50px;
        margin-top: 10px;
    }
    .line{
        width:40px;
        height:3px;
        background-color: white;
        margin:5px;
    }
    .nav-link{
        width: 100%;
    }
    .nav-links.open{
        clip-path: circle(1000px at 90% -15%);
        -webkit-clip-path:circle(1000px at 90% -15%);
    }
    /* .nav-links li{
        opacity: 0;
    } */
    .nav-links li a{
        font-size: 25px;
    }
    .nav-links li:nth-child(1){
        transition: all 0.5s ease 0.2s;
    }
    .nav-links li:nth-child(2){
        transition: all 0.5 ease 0.4s;
    }
    .nav-links li:nth-child(3){
        transition: all 0.5 ease 0.6s;
    }
    .nav-links li:nth-child(4){
        transition: all 0.5 ease 0.8s;
    }
    .nav-links li:nth-child(5){
        transition: all 0.5 ease 0.10s;
    }
    li.fade{
        opacity:1;
    }
}
.container-slider {

    width:100%;
    height:100vh;
    /* margin: 100px auto 0; */
    position: relative;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  /* @media screen and (max-width: 700px){
    .container-slider {
      margin: 100px 10px 0;
    }
  } */
  .slide {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;
  }
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .active-anim {
    opacity: 1;
  }
  
  .btn-slide {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  
  .btn-slide img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .prev {
    top: 50%;
    left: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
  }
  .next {
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-60%);
            transform: translateY(-60%);
  }
  
  
  .container-dots {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
  }
  .dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f1;
  }
  .dot.active {
    background: rgb(32, 32, 32);
  }
