    font-weight: 300;
}
.containers .contactinfo ul.sci{
    position: relative;
    display: flex;
}
.containers .contactinfo ul.sci li{
    list-style: none;
    margin-right: 15px;
}
.containers .contactinfo ul.sci li a{
    text-decoration: none;
    color: #fff;
}
.fab{   
    font-size: 30px;
}
.containers .contactform{
    position: absolute;
    padding: 70px 50px;
    padding-left: 250px;
    margin-left: 150px;
    width:calc(100% - 150px);
    height: 100%;
    background-color: #fff;
    box-shadow: 0 50px 50px rgba(0, 0, 0,0.25);
}
.containers .contactform h2{
    color:#24262b;
    font-size: 32px;
    font-weight: 500;
}
.containers .contactform .formbox{
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 30px;
}
.containers .contactform .formbox .inputbox{
    color:black;
    position: relative;
    margin-bottom: 35px;
}
.containers .contactform .formbox .inputbox.w50{
    width:47%;
}
.containers .contactform .formbox .inputbox.w100{
    width:100%;
}
.containers .contactform .formbox .inputbox input,
.containers .contactform .formbox .inputbox textarea{
    width: 100%;
    resize: none;
    padding: 5px 0;
    font-size: 18px;
    font-weight: 300;
    color: #333;
    border: none;
    outline:none;
    border-bottom: 1px solid #777;
}
 .containers .contactform .formbox .inputbox textarea{
    height: 120px;
}
.containers .contactform .formbox .inputbox span{
    position: absolute;
    left: 0;
    padding:5px 0;
    pointer-events: none;
    font-size: 18px;
    font-weight: 300;
    transition: 0.3s;
}
.containers .contactform .formbox .inputbox input:focus ~ span,
.containers .contactform .formbox .inputbox textarea:focus ~ span,
.containers .contactform .formbox .inputbox input:valid ~ span,
.containers .contactform .formbox .inputbox textarea:valid ~ span{
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    font-size: 15px;
    font-weight: 400;
    letter-spacing: 1px;
    color:#24262b;
    font-weight: 500; 
}
.containers .contactform .formbox .inputbox input[type="submit"]{
    position: relative;
    cursor: pointer;
    background:#24262b;
    color:#fff;
    border:None;
    max-width: 150px;
    padding: 12px;
}
.containers .contactform .formbox .inputbox input[type="submit"]:hover{
    background: #374440;
}
@media (max-width:1200px){
    .containers{
        width:90%;
        min-width: auto;
        margin: 20px;
        box-shadow: 0 20px 50px rgba(0, 0, 0, 0.2);
    }
    .containers .contactinfo{
        top:0;
        height: 500px;
